.tickerlist {
    position:relative;
    margin: auto;
    width: 60%;
    padding: 10px;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 1 1;
      flex: 1 1;
  height: 7%;
  background:#F0F0F0;
  border:1px solid #CCC;
  width:100%;
  
  z-index: 1;
}

.header-buttons {
  -ms-flex-item-align: center;
      align-self: center;
  margin-top: auto;
}

.title {
  margin-top: auto;
  -ms-flex-item-align: start;
      align-self: flex-start;
  font-size: 1.75em;
}

.searchbar-wrapper {
  text-align: center;
  top: 5vh;
}

.searchbar {
  display: inline-block;
}

svg .line-chart__axis-tick,
svg .line-chart__axis-line {
  stroke: darkgray;
}
svg .line-chart__axis-text,
svg .line-chart__axis-text {
  font-family: sans-serif;
  font-size: 11px;
}
svg .line-chart__area-plot {
  stroke: steelblue;
  stroke-width: 3px;
}

.linechart p {
  text-align: center;
}


